import React, {Component} from 'react';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';

class SquarePictures extends Component {
    constructor (props) {
        super(props);
        this.state = {
            price: false,
            value: false,
            error: false,
            height: false,
            width: false,
        }
    }
    showPrice = () => {
        let height = this.refs.height.querySelector('input').value;
        let width = this.refs.width.querySelector('input').value;
        let plosad = width * height;
        plosad = Math.floor(plosad);
        let coast = plosad * 2.9;
        if (plosad >= 3848) {
            coast = coast * 1.5
        } else if (plosad >= 2827) {
            coast = coast * 1.4
        } else if (plosad >= 1963 ) {
            coast = coast * 1.33
        } else if (plosad >= 1256) {
            coast = coast * 1.1
        }
        coast = coast - (coast % 100) + 100;

        if (height <= 24 && width <= 24) {
            coast = `Вы указали слишком маленькую высоту ${height}см и длину ${width}см`;
            this.setState({
                price: coast,
                error: true
            });
        } else if ( width <= 24) {
            coast = `Вы указали слишком маленькую длину ${width}см`;
            this.setState({
                price: coast,
                error: true
            });
        } else if (height <= 24 ) {
            coast = `Вы указали слишком маленькую высоту ${height}см`;
            this.setState({
                price: coast,
                error: true
            });
        } else {
            coast = (coast - (coast % 100) + 100) + ' рублей';
            this.setState({
                price: coast,
                height: height,
                width: width,
                error: false
            });
        }
    }
    render () {
        return (
            <Container max-width="md">
            <Paper className="padding-paper" elevation={1}>
            <Grid
                container
                direction="column"
                justify="center"
                alignItems="center"
                spacing={1}
            >
                <Grid item>
                    <Typography className="align-center" component="p" variant="h5">
                        Рассчёт стоимости квадратной картины
                    </Typography>
                    <Typography className="align-center" component="p" variant="subtitle2">
                        Ширина и длина картины не должны быть меньше 25см
                    </Typography>
                </Grid>
                <Grid item><Typography component="p" variant="subtitle2">Введите размеры картины:</Typography></Grid>
                <Grid item><Typography component="p" variant="subtitle2">Ширина</Typography><Input type="number" ref="width" defaultValue={50} /></Grid>
                <Grid item><Typography component="p" variant="subtitle2">Высота</Typography><Input type="number" ref="height" defaultValue={50} /></Grid>
                <Grid item><Button onClick={this.showPrice} color="default" variant="outlined" >Узнать стоимость</Button></Grid>
                {this.state.price && 
                    <Grid item><Typography component="h5" color={this.state.error ? 'error' : 'initial'} variant={this.state.error ? 'subtitle2' : 'h6'} >
                    {!this.state.error ? `Картина шириной ${this.state.width}см и высотой ${this.state.height}см будет равна ${this.state.price}`: this.state.price}
                    </Typography></Grid>
                }
            </Grid>
            </Paper>
            </Container>
        )
    }
}

export default SquarePictures;


