import React, {Component} from 'react';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';

class CirclePictures extends Component {
    constructor (props) {
        super(props);
        this.state = {
            price: false,
            value: false,
            error: false,
            diametr: false
        }
    }
    showPrice = () => {
        let diametr = this.refs.circleSize.querySelector('input').value;
        let plosad = 0.25 * 3.14 * (diametr * diametr);
        plosad = Math.floor(plosad);
        let coast = plosad * 2.9;
        if (diametr >= 70) {
            coast = coast * 1.5
        } else if (diametr >= 60) {
            coast = coast * 1.4
        } else if (diametr >= 50 ) {
            coast = coast * 1.33
        } else if (diametr >= 40) {
            coast = coast * 1.1
        }
        if (diametr > 24) {
            coast =(coast - (coast % 100) + 100);
            this.setState({
                price: coast,
                diametr: diametr,
                error: false
            });
        } else {
            coast = 'Вы указали слишком маленький диаметр';
            this.setState({
                price: coast,
                diametr: diametr,
                error: true
            });
        }
    }
    render () {
        return (
            <Container max-width="md">
                <Paper className="padding-paper" elevation={1}>
                    <Grid
                        container
                        direction="column"
                        justify="center"
                        alignItems="center"
                        spacing={1}
                    >
                        <Grid item>
                            <Typography className="align-center" component="p" variant="h5">
                                Рассчёт стоимости круглой картины
                            </Typography>
                            <Typography className="align-center" component="p" variant="subtitle2">
                                Диаметр картины не должен быть меньше 25см
                            </Typography>
                        </Grid>
                        <Grid item><Typography component="p" variant="subtitle2">Введите диаметр картины:</Typography></Grid>
                        <Grid item><Input type="number" ref="circleSize" defaultValue={50} /></Grid>
                        <Grid item><Button onClick={this.showPrice} color="default" variant="outlined" >Узнать стоимость</Button></Grid>
                        {this.state.price && 
                            <Grid item><Typography component="h5" color={this.state.error ? 'error' : 'initial'} variant={this.state.error ? 'subtitle2' : 'h6'} >
                                {this.state.error ? `Вы указали слишком маленький диаметр - ${this.state.diametr}см` : `Стоимость картины диаметром ${this.state.diametr}см равна ${this.state.price} рублей`}
                            </Typography></Grid>
                        }
                    </Grid>
                </Paper>
            </Container>
        )
    }
}

export default CirclePictures;


