import React from "react";
import CirclePictures from './circlepictures';
import SquarePictures from './squarePictures';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';

class CalcApp extends React.Component {
  render () {
    return (
      <Container maxWidth="md">
        <Grid  
        container
        direction="column"
        justify="center"
        alignItems="stretch" 
        spacing={3}>
          <Grid item>
            <CirclePictures />
          </Grid>
          <Grid item>
            <SquarePictures />
          </Grid>
        </Grid>
      </Container>
    );
  }
}

export default CalcApp;


